html {
  font-size: 62.5%;
  box-sizing: border-box;
}

body {
  min-height: 100%;
  color: #FFFFFF;
  font-family: "Roboto", sans-serif;
  overflow-x: hidden;
}

*, *:before, *:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  width: 85%;
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  margin: 5rem auto;
  margin-top: 12rem;
}
@media (max-width: 768px) {
  .section {
    margin-top: 5rem;
  }
}

a {
  text-decoration: none;
  color: #FFFFFF;
}

ul, li {
  list-style: none;
}

img {
  max-width: 100%;
  width: inherit;
  height: inherit;
}

#bg {
  position: fixed;
  z-index: -1;
  width: 100%;
  height: 100%;
  background: #16222A;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #162f3a, #0d151a);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #162f3a, #0d151a);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.title {
  font-size: 4rem;
  text-align: start;
  width: 100%;
}
@media (max-width: 768px) {
  .title {
    text-align: center;
  }
}
@media (max-width: 480px) {
  .title {
    font-size: 2.5rem;
  }
}

.activeLink {
  color: #3A6073 !important;
}

.disclaimer {
  opacity: 0;
}

h1, h2, h3 {
  font-family: "Roboto", sans-serif;
  margin: 0;
  font-weight: 400;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
