.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 2rem;
  z-index: 1;
  transition: all 0.7s ease;
}
.header .logo-container .logo {
  width: 25rem;
  height: 10rem;
}
.header .logo-container .logo rect {
  transition: stroke 0.3s ease;
}
@media (max-width: 480px) {
  .header .logo-container .logo {
    width: 20rem;
    height: 10rem;
  }
}
.header .logo-container .logo .logoPath {
  fill: #FFFFFF;
}
.header .menu-icon {
  font-size: 4rem;
  display: none;
  z-index: 20;
}
@media (max-width: 768px) {
  .header .menu-icon {
    display: inline-block;
  }
}

.down {
  background-color: #3A6073;
  padding: 0rem 2rem;
}
.down .logo rect {
  stroke: #FFFFFF;
}

.nav ul {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.nav ul li a {
  color: #FFFFFF;
  padding: 2rem;
  font-size: 2rem;
  position: relative;
  display: block;
  z-index: 99;
  transition: all 0.3s ease;
}
.nav ul li a:before {
  content: "";
  display: block;
  width: 0%;
  height: 0.3rem;
  background-color: #FFFFFF;
  position: absolute;
  left: 0;
  bottom: 0;
  transition: all 0.3s ease;
}
.nav ul li a:hover {
  cursor: pointer;
  color: #6396b0;
}
.nav ul li a:hover:before {
  width: 100%;
}
@media (max-width: 768px) {
  .nav {
    background-color: #3A6073;
    transform: translate(-100%);
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease;
  }
  .nav ul {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5rem;
  }
  .nav.active {
    transform: translate(0);
  }
}

.cards .title-cards-container {
